// ** React Imports
import { Link } from 'react-router-dom'
import { Can } from '@src/utility/context/Can.js'
// ** Custom Components
import Avatar from '@components/avatar'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

// ** Store & Actions
import { store } from '@store/store'
import { getClient, deleteClient, updateStatus } from '../store'

// ** Icons Imports
import { MoreVertical, Trash2, Eye, XCircle, CheckCircle, Edit } from 'react-feather'

// ** Reactstrap Imports
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Col } from 'reactstrap'
const MySwal = withReactContent(Swal)
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
// ** Renders Client Columns
const renderClient = name => {
  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={name ? name.charAt(0).toUpperCase() : ''}
    />
  )
}

const statusObj = [
  '',
  'light-primary',
  'light-warning'
]

const statusArr = [
  '',
  "Active",
  "Inactive"

]

const deleteClientfun = (id) => {

  return MySwal.fire({
    title: 'Are you sure?',
    text: "You won't be able to revert this!",
    icon: 'warning',
    showCancelButton: true,
    confirmButtonText: 'Yes, delete it!',
    customClass: {
      confirmButton: 'btn btn-primary',
      cancelButton: 'btn btn-outline-danger ms-1'
    },
    buttonsStyling: false
  }).then(async (result) => {
    if (result.value) {
      await store.dispatch(deleteClient(id))
      MySwal.fire({
        icon: 'success',
        title: 'Deleted!',
        text: 'Client has been deleted.',
        customClass: {
          confirmButton: 'btn btn-success'
        }
      })
    }
  })
}

export const columns = [
  {
    name: 'Business Name',
    minWidth: '230px',
    sortable: true,
    sortField: 'name',
    selector: row => row.name,
    cell: row => (row.name !== '' ? <div className='d-flex justify-content-left align-items-center'>
      {renderClient(row.name)}
      <div className='d-flex flex-column'>
        <Link
          to={`/clients/view/${row.id}`}
          className='user_name text-truncate text-body'
          onClick={() => store.dispatch(getClient(row.id))}
        >
          <span className='fw-bolder'>{row.name === '' ? row.contactpersonname : row.name}</span>
        </Link>
      </div>
    </div> : <span>-</span>
    )
  },
  {
    name: 'Contact Person',
    sortable: true,
    minWidth: '300px',
    sortField: 'email',
    selector: row => row.contactpersonname,
    cell: row => (<Link to={`/clients/view/${row.id}`}>
      <div className='d-flex justify-content-left align-items-center text-body'>
        {renderClient(row.contactpersonname)}
        <div className='d-flex flex-column'>
          <span className='fw-bolder'>{row.contactpersonname}</span>
          <small className='text-truncate text-muted mb-0'>{row.email}</small>
        </div>
      </div>
    </Link>
    )
  },
  {
    name: 'Contact',
    minWidth: '138px',
    maxWidth: '150px',
    sortable: true,
    sortField: 'contactnumber',
    selector: row => row.contactnumber,
    cell: row => <Link to={`/clients/view/${row.id}`}> <span className='text-capitalize text-body'>{row.contactnumber}</span></Link>
  },
  {
    name: 'Status',
    minWidth: '138px',
    maxWidth: '150px',
    sortable: true,
    sortField: 'status',
    selector: row => statusArr[row.status],
    cell: row => (
      <Link to={`/clients/view/${row.id}`}>
        <Badge className='text-capitalize' color={statusObj[row.status]} pill>
          {statusArr[row.status]}
        </Badge>
      </Link>
    )
  },
  {
    name: 'Actions',
    minWidth: '100px',
    maxWidth: '150px',
    cell: row => (
      <div className='column-action d-flex align-items-center'>
        <Can I={'read'} a={'client'}>
          <Col tag={Link} lg={4}
            to={`/clients/view/${row.id}`}
            onClick={() => store.dispatch(getClient(row.id))}>
            <Eye className='cursor-pointer mt-0' size={16} />
          </Col>
        </Can>
        <Can I={'write'} a={'client'}>
          <Col lg={4} tag={Link} to={`/clients/edit/${row.id}`}
            onClick={() => store.dispatch(getClient(row.id))}>
            <Edit
              className='cursor-pointer ms-1 mt-0' size={16} />
          </Col>
        </Can>
        <UncontrolledDropdown>
          <DropdownToggle tag='div' className='btn btn-sm'>
            <MoreVertical size={14} className='cursor-pointer' />
          </DropdownToggle>
          <DropdownMenu>
            {row.status === 2 && (
              <Can I={'write'} a={'client'}>
                <DropdownItem tag='a' href='/' className='w-100' onClick={e => {
                  e.preventDefault()
                  const id = row.id
                  const status = 1
                  const obj = { id, status, updatedBy: userId }
                  store.dispatch(updateStatus(obj))
                }}>
                  <CheckCircle size={14} className='me-50' />
                  <span className='align-middle'>Mark as Active</span>
                </DropdownItem>
              </Can>
            )
            }
            {row.status === 1 && (
              <Can I={'write'} a={'client'}>
                <DropdownItem tag='a' href='/' className='w-100' onClick={e => {
                  e.preventDefault()
                  const id = row.id
                  const status = 2
                  const obj = { id, status, updatedBy: userId }
                  store.dispatch(updateStatus(obj))
                }}>
                  <XCircle size={14} className='me-50' />
                  <span className='align-middle'>Mark as Inactive</span>
                </DropdownItem>
              </Can>
            )
            }
            <Can I={'delete'} a={'client'}>
              <DropdownItem
                tag='a'
                href='/'
                className='w-100'
                onClick={e => {
                  e.preventDefault()
                  deleteClientfun(row.id)
                }}
              >
                <Trash2 size={14} className='me-50' />
                <span className='align-middle'>Delete</span>
              </DropdownItem>
            </Can>
          </DropdownMenu>
        </UncontrolledDropdown>
      </div>
    )
  }
]
