// ** React Imports
import { Fragment, useState, useEffect, useContext, forwardRef } from 'react'
import { Link } from 'react-router-dom'

// ** Table Columns
import { columns } from './columns'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { AbilityContext } from '@src/utility/context/Can'
// ** Store & Actions
import { getData, updateBulk, exportClient } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import axios from '@src/configs/axios/axiosConfig'

// ** Third Party Components
import Select from 'react-select'
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { CheckCircle, ChevronDown, MoreVertical, Download, Trash, UploadCloud, XCircle } from 'react-feather'

// ** Utils
import { selectThemeColors } from '@utils'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  UncontrolledTooltip,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, selectedIds, exportList }) => {

  const dispatch = useDispatch()
  const statusArr = ['', 'Active', 'Inactive']
  const ability = useContext(AbilityContext)
  const statusUpdatefn = (status) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: `Yes, ${status === -1 ? 'Deleted' : `Mark as ${statusArr[status]}`} it!`,
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(updateBulk({ status, selectedIds }))
        MySwal.fire({
          icon: 'success',
          title: `${status === -1 ? 'Deleted' : statusArr[status]}`,
          text: `Client has been ${status === -1 ? 'Deleted' : statusArr[status]}`,
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  return (
    <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
      <Row>
        <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
          <div className='d-flex align-items-center w-100'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>
            {selectedIds.length > 0 && (
              <div>
                {ability.can('delete', 'client') &&
                  <Button.Ripple className='btn-icon rounded-circle ms-1' color='danger' id="delete_btn" onClick={() => { statusUpdatefn(-1) }}>
                    <Trash size={16} />
                  </Button.Ripple>
                } {ability.can('delete', 'client') &&
                  <UncontrolledTooltip target='delete_btn'>Delete</UncontrolledTooltip>

                }
                {ability.can('write', 'client') &&
                  <Button.Ripple className='btn-icon rounded-circle ms-1' color='success' id="markas_active_btn" onClick={() => { statusUpdatefn(1) }}>
                    <CheckCircle size={16} />
                  </Button.Ripple>
                }{ability.can('write', 'client') &&
                  <UncontrolledTooltip target='markas_active_btn'>Mark as Active</UncontrolledTooltip>
                }
                {ability.can('write', 'client') &&
                  <Button.Ripple className='btn-icon rounded-circle ms-1' color='warning' id="markas_inactive_btn" onClick={() => { statusUpdatefn(2) }}>
                    <XCircle size={16} />
                  </Button.Ripple>
                }{ability.can('write', 'client') &&
                  <UncontrolledTooltip target='markas_inactive_btn'>Mark as Inactive</UncontrolledTooltip>
                }
              </div>
            )}
          </div>
        </Col>

        <Col md='6' sm='6'
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>

            {ability.can('read', 'client') &&
              <Button color='primary' tag={Link} to='/clients/add'>
                Add Client
              </Button>
            }
            <UncontrolledDropdown className='ms-1'>
              <DropdownToggle color='secondary' caret outline>
                <MoreVertical className='font-small-4 me-50' />
              </DropdownToggle>
              <DropdownMenu>
                {ability.can('import', 'client') &&
                  <DropdownItem className='w-100' tag={Link} to='/import/clients'>
                    <UploadCloud className='font-small-4 me-50' />
                    <span className='align-middle'>Import</span>
                  </DropdownItem>
                }
                {ability.can('export', 'client') &&
                  <DropdownItem className='w-100' onClick={() => { exportList() }} >
                    <Download className='font-small-4 me-50' />
                    <span className='align-middle'>Export</span>
                  </DropdownItem>
                }
              </DropdownMenu>
            </UncontrolledDropdown>
          </div>
        </Col>
      </Row>
    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.client)

  // ** Bootstrap Checkbox Component
  const BootstrapCheckbox = forwardRef((props, ref) => (
    <div className='form-check'>
      <Input type='checkbox' ref={ref} {...props} />
    </div>
  ))
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  // ** States
  const [sort, setSort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn, setSortColumn] = useState('createdon')
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [businessEntity, setBusinessEntity] = useState({ id: '', name: 'Select Entity' })
  const [currentStatus, setCurrentStatus] = useState({ id: 1, name: 'Active' })

  // ** Client filter options
  const [businessEntityOptions, setBusinessEntityOptions] = useState([])
  const statusOptions = [
    { id: '', name: 'Select All' },
    { id: 1, name: 'Active' },
    { id: 2, name: 'Inactive' }
  ]

  const getBusinessEntity = () => {
    axios.post(`/businessentities/list`).then(response => {
      const arr = response.data.businessentities
      arr.unshift({ id: '', name: 'Select All' })
      setBusinessEntityOptions(arr)

    })
  }

  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        businessEntityId: businessEntity.id
      })
    )
    setBlock(false)
  }, [dispatch])

  useEffect(() => {
    getBusinessEntity()
  }, [])

  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: page.selected + 1,
        status: currentStatus.id,
        businessEntityId: businessEntity.id
      })
    )
    setBlock(false)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    const value = parseInt(e.currentTarget.value)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: value,
        page: currentPage,
        businessEntityId: businessEntity.id,
        status: currentStatus.id
      })
    )
    setBlock(false)
    setRowsPerPage(value)
  }

  const exportList = async () => {
    setBlock(true)
    await dispatch(
      exportClient({
        sort,
        sortColumn,
        q: searchTerm,
        businessEntityId: businessEntity.id,
        status: currentStatus.id
      })
    )
    setBlock(false)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        businessEntityId: businessEntity.id
      })
    )
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      businessEntityId: businessEntity.id,
      status: currentStatus.id,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return store.allData.slice(0, rowsPerPage)
    }
  }

  const handleSort = (column, sortDirection) => {
    setSort(sortDirection)
    setSortColumn(column.sortField)
    dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        businessEntityId: businessEntity.id
      })
    )
  }
  const [selectedIds, setSelectedIds] = useState([])
  const handleChange = (state) => {
    const temp = []
    state.selectedRows.forEach((obj) => {
      temp.push(obj.id)
    })
    setSelectedIds(temp)
  }

  return (
    <Fragment>
      <Card>
        <CardBody>
          <Row>
            <Col md='4'>
              <Label for='status-select'>Status</Label>
              <Select
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                value={currentStatus}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setCurrentStatus(data)
                  setBlock(true)
                  await dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      page: currentPage,
                      perPage: rowsPerPage,
                      businessEntityId: businessEntity.id,
                      status: data.id
                    })
                  )
                  setBlock(false)
                }}
              />
            </Col>
            <Col className='my-md-0 my-1' md='4'>
              <Label for='plan-select'>Business Entity</Label>
              <Select
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={businessEntityOptions}
                value={businessEntity}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setBusinessEntity(data)
                  setBlock(true)
                  await dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      page: currentPage,
                      perPage: rowsPerPage,
                      businessEntityId: data.id,
                      status: currentStatus.id
                    })
                  )
                  setBlock(false)
                }}
              />
            </Col>
          </Row>
        </CardBody>
      </Card>

      <Card className='overflow-hidden'>
        <div className='react-dataTable'>
          <UILoader blocking={block} loader={<Loader />}>
            <DataTable
              noHeader
              subHeader
              sortServer
              pagination
              responsive
              highlightOnHover
              selectableRows
              paginationServer
              columns={columns}
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              className='react-dataTable'
              paginationComponent={CustomPagination}
              data={dataToRender()}
              selectableRowsComponent={BootstrapCheckbox}
              onSelectedRowsChange={handleChange}
              subHeaderComponent={
                <CustomHeader
                  selectedIds={selectedIds}
                  store={store}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                  exportList={exportList}
                />
              }
            />
          </UILoader>
        </div>
      </Card>

    </Fragment>
  )
}

export default UsersList
